* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

h2 {
  margin-bottom: 0;
  margin-top: 0;
}

ul {
  padding: 0;
}

.country-contaiter {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.country-img {
  margin-right: 20px;
}

.description {
  font-weight: 700;
}
